<template>
  <div id="app">
    <audio src="./static/music.mp3" autoplay id="music" hidden loop>
    </audio>
    <img @click="control" id="control" class="control rotate" src="./assets/imgs/music.png"/>
    <activity/>
  </div>
</template>

<script>
  import activity from './components/activity'

  export default {
    name: 'App',
    data: function () {
      return {
        audio: ''
      }
    },
    mounted: function () {
      document.addEventListener('WeixinJSBridgeReady', function () {
        document.getElementById('music').play();
      }, false);
    },
    components: {
      activity
    },
    methods: {
      control: function () {
        if (!this.audio) {
          this.audio = document.getElementById('music');
        }
        if (this.audio !== null) {
          if (this.audio.paused) {
            this.audio.play();
            $('#control').addClass('rotate')
          } else {
            this.audio.pause();
            $('#control').removeClass('rotate')
          }
        }
      }
    }
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 14px;
  }

  html, body, #app {
    width: 100%;
    height: 100%;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
    overflow: hidden;
  }

  .control {
    display: inline-block;
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
    z-index: 3;
    width: 0.6rem;
    height: 0.6rem;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0deg);
      box-shadow: 0px 0px 10px #fff;
    }
    50% {
      transform: rotate(180deg);
      box-shadow: 0px 0px 45px #fff;
    }
    100% {
      transform: rotate(360deg);
      box-shadow: 0px 0px 10px #fff;
    }
  }

  .rotate {
    animation: rotate 5s linear infinite;
    border-radius: 50%;
  }
</style>



