<template>
  <div class="activity-wrapper" id="activity-wrapper">
    <div class="page">
      <img class="background" src="../assets/imgs/firstPage/background.jpg"/>
      <img class="btn4" src="../assets/imgs/firstPage/btn.png" @click="showDialog"/>
      <div id="dialog" class="dialog" style="z-index: 5;display: none" @click="goStep(1)">
        <img class="dialog-bg" src="../assets/imgs/firstPage/dialog.png"/>
        <div class="dialog-box">
          HiJDer9042B11:30~~
        </div>
      </div>
    </div>
    <div v-for="(item, index) in question" :key="index" class="page">
      <img class="background" src="../assets/imgs/questionPage/background.jpg"/>
      <template v-if="item.isLastOne">
        <img class="btn2" src="../assets/imgs/questionPage/submit.png" @click="submit"/>
      </template>
      <template v-else>
        <img class="btn3" src="../assets/imgs/questionPage/btn-next.png" @click="goStep(item.step+1)"/>
      </template>
      <div class="question-box">
        <div class="question-title">{{item.title}}</div>
        <div class="question-item" @click="selectItem(item,item.A,'A')">
          <template v-if="item.A.selected">
            <img class="question-item-bg" src="../assets/imgs/questionPage/item-selected.png"/>
          </template>
          <template v-else>
            <img class="question-item-bg" src="../assets/imgs/questionPage/item.png"/>
          </template>
          <span class="question-item-text">A{{item.A.text}}</span>
        </div>
        <div class="question-item" @click="selectItem(item,item.B,'B')">
          <template v-if="item.B.selected">
            <img class="question-item-bg" src="../assets/imgs/questionPage/item-selected.png"/>
          </template>
          <template v-else>
            <img class="question-item-bg" src="../assets/imgs/questionPage/item.png"/>
          </template>
          <span class="question-item-text">B{{item.B.text}}</span>
        </div>
        <div class="question-item" @click="selectItem(item,item.C,'C')">
          <template v-if="item.C.selected">
            <img class="question-item-bg" src="../assets/imgs/questionPage/item-selected.png"/>
          </template>
          <template v-else>
            <img class="question-item-bg" src="../assets/imgs/questionPage/item.png"/>
          </template>
          <span class="question-item-text">C{{item.C.text}}</span>
        </div>
        <div class="question-item" @click="selectItem(item,item.D,'D')">
          <template v-if="item.D.selected">
            <img class="question-item-bg" src="../assets/imgs/questionPage/item-selected.png"/>
          </template>
          <template v-else>
            <img class="question-item-bg" src="../assets/imgs/questionPage/item.png"/>
          </template>
          <span class="question-item-text">D{{item.D.text}}</span>
        </div>
      </div>
    </div>
    <div class="page">
      <img class="background" src="../assets/imgs/lastPage/background.jpg"/>
      <img class="btn2 share" src="../assets/imgs/lastPage/btn2.png" @click="share"/>
      <div class="summary">
        <div class="summary-title"></div>
        <div class="summary-text"><span class="summary-score">{{score}}</span></div>
      </div>
      <div id="dialog-share" class="dialog" style="z-index: 5;display: none" @click="hide">
        <img class="dialog-share-bg" src="../assets/imgs/lastPage/share.png"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        question: [{
          title: '()',
          A: {text: '19', selected: false},
          B: {text: '39', selected: false},
          C: {text: '49', selected: false},
          D: {text: '412', selected: false},
          selected: [],
          answer: [undefined, undefined, 'C'],
          step: 1,
          singleSelect: true
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: ['A', 'B', 'C', 'D'],
          step: 2,
          singleSelect: false
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: ['A', 'B', 'C'],
          step: 3,
          singleSelect: false
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: [undefined, undefined, 'C'],
          step: 4,
          singleSelect: true
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: [undefined, 'B', 'C'],
          step: 5,
          singleSelect: false
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: ['A', 'B', 'C'],
          step: 6,
          singleSelect: false
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: [undefined, 'B'],
          step: 7,
          singleSelect: true
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: ['A'],
          step: 8,
          singleSelect: true
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '8', selected: false},
          D: {text: '10', selected: false},
          selected: [],
          answer: [undefined, undefined, undefined, 'D'],
          step: 9,
          singleSelect: true
        }, {
          title: '()',
          A: {text: '', selected: false},
          B: {text: '', selected: false},
          C: {text: '', selected: false},
          D: {text: '', selected: false},
          selected: [],
          answer: ['A', 'B', 'C', 'D'],
          step: 10,
          isLastOne: true,
          singleSelect: false
        }],
        score: 0
      }
    },
    methods: {
      goStep: function (n) {
        $('#activity-wrapper').animate({top: `-${n * 100}%`});
      },
      showDialog: function () {
        $('#dialog').show();
      },
      submit: function () {
        this.question.forEach((v) => {
          let flag = true;
          if (v.selected.length === 0) {
            flag = false;
          }
          v.answer.forEach((val, index) => {
            if (v.selected[index] !== val) {
              flag = false;
            }
          });
          if (flag) {
            this.score += 10;
          }
        });
        this.goStep(11)
      },
      selectItem: function (questionObj, itemObj, type) {
        itemObj.selected = !itemObj.selected;
        switch (type) {
          case 'A':
            itemObj.selected ? questionObj.selected[0] = 'A' : questionObj.selected[0] = undefined;
            if (questionObj.singleSelect) {
              questionObj.B.selected = questionObj.C.selected = questionObj.D.selected = false;
              questionObj.selected[1] = questionObj.selected[2] = questionObj.selected[3] = undefined;
            }
            break;
          case 'B':
            itemObj.selected ? questionObj.selected[1] = 'B' : questionObj.selected[1] = undefined;
            if (questionObj.singleSelect) {
              questionObj.A.selected = questionObj.C.selected = questionObj.D.selected = false;
              questionObj.selected[0] = questionObj.selected[2] = questionObj.selected[3] = undefined;
            }
            break;
          case 'C':
            itemObj.selected ? questionObj.selected[2] = 'C' : questionObj.selected[2] = undefined;
            if (questionObj.singleSelect) {
              questionObj.A.selected = questionObj.B.selected = questionObj.D.selected = false;
              questionObj.selected[0] = questionObj.selected[1] = questionObj.selected[3] = undefined;
            }
            break;
          case 'D':
            itemObj.selected ? questionObj.selected[3] = 'D' : questionObj.selected[3] = undefined;
            if (questionObj.singleSelect) {
              questionObj.A.selected = questionObj.B.selected = questionObj.C.selected = false;
              questionObj.selected[0] = questionObj.selected[1] = questionObj.selected[2] = undefined;
            }
            break;
        }
      },
      share: function () {
        $('#dialog-share').show();
      },
      hide: function () {
        $('#dialog-share').hide();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .activity-wrapper {
    position: absolute;
  }

  .activity-wrapper .page {
    height: 100vh;
    position: relative;
  }

  .background {
    width: 100vw;
    height: 100vh;
    z-index: -1;
  }

  .btn4 {
    width: 2.37rem;
    height: 0.62rem;
    position: absolute;
    bottom: 0.55rem;
    left: 0.70rem;
  }

  .btn3 {
    width: 1.8rem;
    height: 0.64rem;
    position: absolute;
    bottom: 0.35rem;
    left: 0.95rem;
  }

  .btn2 {
    width: 1.6rem;
    height: 0.65rem;
    position: absolute;
    bottom: 0.35rem;
    left: 0.95rem;
  }

  .dialog {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.9;
    background-color: #595B5D;
  }

  .dialog-bg {
    z-index: -1;
  }

  .dialog-bg, .dialog-box {
    display: inline-block;
    width: 3.35rem;
    height: 2.48rem;
    position: absolute;
    left: 50vw;
    top: 50vh;
    margin-left: -1.675rem;
    margin-top: -1.24rem;
  }

  .dialog-box {
    font-size: 16px;
    font-weight: bold;
    padding: 0.6rem 0.35rem 0.35rem 0.35rem;
    text-indent: 2em;
  }

  .close {
    position: absolute;
    top: 0.3rem;
    right: 0.3rem;
    width: 0.25rem;
    height: 0.25rem;
  }

  .question-box {
    position: absolute;
    top: 33vh;
    left: 0.45rem;
  }

  .question-item {
    margin-bottom: 0.1rem;
  }

  .question-title {
    font-size: 16px;
    font-weight: bold;
    margin: 0.25rem 0 0.2rem;
  }

  .question-item-text {
    z-index: 1;
    position: absolute;
    line-height: 0.49rem;
    padding-left: 0.3rem;
  }

  .question-item-text, .question-item, .question-item-bg {
    width: 2.83rem;
    height: 0.49rem;
  }

  .question-item-bg {
    position: absolute;
  }

  .dialog-share-bg {
    width: 1.19rem;
    height: 0.93rem;
    position: absolute;
    right: 0.2rem;
    top: 0.3rem;
    transform: rotate(-10deg);
  }

  .summary {
    position: absolute;
    left: 0.7rem;
    top: 1.1rem;
  }

  .summary-title {
    font-size: 28px;
    color: #F5A90A;
    margin-left: 0.7rem;
    margin-top: 0.08rem;
  }

  .summary-text {
    font-size: 28px;
    color: #5B050B;
    margin-left: 0.55rem;
    margin-top: 0.11rem;
  }

  .summary-score {
    font-size: 32px;
    color: #F5A90A;
  }

  .btn2.share {
    bottom: 0.25rem;
  }
</style>



